import { connect } from "react-redux"
import { Redirect } from "react-router-dom"
import {Field, reduxForm} from "redux-form"
import { login } from "../../redux/authReducer"
import { Input } from "../common/FormsControls/FormsControls"
import { maxLengthCreator, required } from "../utils/validators/validators"
import style from "../common/FormsControls/formControl.module.css"
let maxLen50 = maxLengthCreator(50)
const LoginForm = (props)=> {
    return(
        <form onSubmit={props.handleSubmit}>
            <div><Field component={Input} name={"email"} placeholder={"email"} type="text" validate={[required,maxLen50]} /></div>
            <div><Field component={Input} name={"password"} placeholder={"Password"} type="password" validate={[required,maxLen50]} /></div>
            <div><Field component={"input"} name={"rememberMe"} type={"checkbox"} id="" />remember me </div>
            {props.error && <div className={style.formSummaryError}>{props.error}</div>}
            <div><button>login</button></div>
        </form>
    )
    
}
let LoginReduxForm = reduxForm({
    form:"login"
})(LoginForm)
const Login = (props)=> {
    const onSubmit = (frDa)=> {
        props.login(frDa.email,frDa.password,frDa.rememberMe)
        console.log(frDa)
    }
    if(props.isAuth){
        return <Redirect to={"/profile"}/>
    }
    return <div>
        <h1>Login</h1>
        <LoginReduxForm onSubmit={onSubmit}/>
    </div>
}
const mapStateToProps = (state) => {
    return {
        isAuth:state.auth.isAuth
    }
}
export default connect(mapStateToProps,{login})(Login)